	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/the-terminator-fiction/index.md absPath of file >>> MarkdownRemark","html":"<img class=\"alignnone size-full wp-image-245\" title=\"terminator\" src=\"http://uploads.psyked.co.uk/2008/06/terminator.jpg\" alt=\"\" width=\"500\" height=\"280\">\n<p>Surfing BBCi's interactive tv services, I couldn't help take notice of one ominous sounding news report - \"Skynet military launch postponed\" - yee gads! <em>\nSkynet</em> ?!</p>\n<p>It gets better with this quote;</p>\n<blockquote>Software on the launch vehicle did not behave normally during a test sequence.</blockquote>\n<em>Did not behave normally</em> (!) Suddenly I feel less worried about America's Cyber warfare division (due to become active on October 1st) and more worried about our own military systems.  I've always thought that the best way to make people believe reality is fiction was to make a movie about it (nobody believes movies are real, right?) and here we are, tempting fate. Uh oh.\n<p><a href=\"http://news.bbc.co.uk/1/hi/sci/tech/7419751.stm\" target=\"_blank\">See the full BBCi report here.</a></p>","frontmatter":{"title":"The Terminator: Fiction?","date":"June 07, 2008","tags":["bbci"]}}},"pathContext":{}}

